<template>
  <div id='create'>
    <div class="weui-cells weui-cells_form">
      <div class="weui-cell">
        <div class="weui-cell__hd"><label for="name" class="weui-label">学校</label></div>
        <div class="weui-cell__bd">
          <input class="weui-input" id="school" type="text" value="">
        </div>
      </div>
      <div class="weui-cell">
        <div class="weui-cell__hd"><label for="name" class="weui-label">寝室区</label></div>
        <div class="weui-cell__bd">
          <input class="weui-input" id="area" type="text" value="">
        </div>
      </div>
    </div>
  <div class="weui-cells weui-cells_form">
    <div class="weui-cell">
      <div class="weui-cell__hd"><label class="weui-label">姓名</label></div>
      <div class="weui-cell__bd">
        <input class="weui-input" type="" placeholder="请输入姓名" id="r1">
      </div>
    </div>
    <div class="weui-cell">
      <div class="weui-cell__hd"><label class="weui-label">手机号</label></div>
      <div class="weui-cell__bd">
        <input class="weui-input" type="number" pattern="[0-9]*" placeholder="请输入手机号" id="r2">
      </div>
    </div>
    <div class="weui-cell">
      <div class="weui-cell__hd"><label class="weui-label">楼栋寝室号</label></div>
      <div class="weui-cell__bd">
        <input class="weui-input" type="" placeholder="例:7-6-xxx" id="r3">
      </div>
    </div>
  </div>
  <button class="weui-btn weui-btn_primary" @click="createuser">确定</button>
  </div>
</template>

<script type="text/ecmascript-6">
  import '../../../node_modules/jquery-weui/dist/css/jquery-weui.css';
  import '../../../node_modules/jquery-weui/dist/lib/weui.min.css';
  import axios from 'axios';
  import qs from 'qs';
  import $ from 'jquery';
  export default {
    mounted() {
      $('#school').select({
          title: '选择学校',
          items: [ '湖南科技大学', '湘潭大学' ],
          onChange: function(d) {
            console.log(d.values);
            if (d.values === '湖南科技大学') {
              $('#area').select('update', { items: ['南校一区', '南校二区', '南校三区', '南校四区', '北校五区', '北校六区', '北校七区', '北校八区'] });
            } else if (d.values === '湘潭大学') {
              $('#area').select('update', { items: ['金翰林', '北苑', '南苑', '兴湘', '琴湖'] });
            }
          }
      });
      $('#area').select({
          title: '选择宿舍区',
          items: [' '],
          onChange: function(d) {
            console.log(d.values);
        }
      });
    },
    methods: {
      isPoneAvailable(str) {
        var myreg = /^[1][3,4,5,7,8][0-9]{9}$/;
          if (!myreg.test(str)) {
              return false;
          } else {
              return true;
          }
      },
      createuser() {
        var name = document.getElementById('r1');// 获取input的节点
        var phone = document.getElementById('r2');
        var address = document.getElementById('r3');
        var school = document.getElementById('school');
        var area = document.getElementById('area');
        // var v = name.value;//获取input的值
        if (school.value.length === 0) {
          $.toptip('学校不能为空', 'error');
          return;
        }
        if (area.value.length === 0) {
          $.toptip('宿舍区不能为空', 'error');
          return;
        }
        if (name.value.length === 0) {
          $.toptip('姓名不能为空', 'error');
          return;
        }
        if (phone.value.length === 0) {
          $.toptip('电话不能为空', 'error');
          return;
        }
        if (this.isPoneAvailable(phone.value)) {
          $.toptip('请输入正确的电话号码', 'error');
          return;
        }
        if (address.value.length === 0) {
          $.toptip('楼栋寝室号不能为空', 'error');
          return;
        }
        const obj = {
          name: name.value,
          mobile: phone.value.toString(),
          district: area.value,
          detail: address.value,
          school: school.value
        };
        var send = qs.stringify(obj);
        console.log(send);
        axios.post('/sell/group/buyer/boxApply', send, {
          headers: {
            'Content-Type': 'application/x-www-form-urlencoded'
          }
        })
          .then(function (response) {
            console.log(response);
            if (response.data.msg === '成功') {
                $.toptip('创建成功', 'success');
            } else {
              window.alert(response.data.msg);
            }
          })
          .catch(function (error) {
            console.log(error);
          });
      },
      getValue() {
        var radio = document.getElementsByName('radio1');
        for (let i = 0; i < radio.length; i++) {
          if (radio[i].checked) {
            return radio[i].value;
          }
        }
      }
    }
  };
</script>

<style lang="stylus" rel="stylesheet/stylus">
</style>



